  import React from "react";
  import {
    Card,
    CardBody,
    OverlayTrigger,
    Tooltip,
  } from "react-bootstrap";
import PostOverviewSkeleton from "../../../layout/skeleton/postOverviewSkeleton";
import PercentDifference from "../../../layout/common/percentDifference";
import SectionHeading from "../../../layout/common/sectionHeading";
import { kFormatter } from "../../../../../../../utils";
import { FontAwesomeIcon } from "../../../../../../CommonComponents/AssetsHelper";


  const PostOverview = (props) => {
    
    const { get_post_overview_loader, get_post_overview_data } = props;

    return (
      <div className="col-lg-4 col-12 mb-4">
        <Card className="sw-card-custom card-shadow">
         <SectionHeading  section={2}  title={'Post Overview'} />
          <CardBody className="px-3">
            { get_post_overview_loader ? <PostOverviewSkeleton/>  : get_post_overview_data && Object.keys(get_post_overview_data).length
              ? get_post_overview_data.map((e) => {
                  return (
                    <div className="py-3 border-bottom mb-3 last-child-mb-0 last-child-border-0">
                      <div className="d-flex align-items-start justify-content-between">
                        <div className="d-flex align-items-start me-4">
                          <div className="">
                            <p className="text-dark mb-2 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name">
                              {e.name}
                              <OverlayTrigger placement="top" overlay={<Tooltip>{e.tooltip}</Tooltip>}>
                               <span className="ms-2"><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-gray-500 fs-6'} /></span>
                              </OverlayTrigger>
                          </p>
                            <p className="mb-0">
                            {e.description}
                            </p>
                          </div>
                        </div>
                        <div className="d-flex align-items-center">
                          <p className="font-weight-boldest text-dark fs-2 m-0">
                            {kFormatter(Math.round(e.count))}
                          </p>
                          <PercentDifference difference={e.difference} flag={e.flag} />
                         </div>
                      </div>
                    </div>
                  );
                })
              : null}

          </CardBody>
        </Card>

      </div>
    );
  };

  export default React.memo(PostOverview)
